import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { Spinner } from 'components/Spinner/Spinner';
import { DialogPage, DialogTitle } from 'components/DialogPage/DialogPage';

// TODO: use actual splendid popcorn animation
const StyledSpinner = styled(Spinner)``;

const StyledSpinnerContainer = styled.div`
    position: absolute;
    top: 0;
    right: 64px;
    bottom: 0;

    width: 816px;

    display: flex;

    align-items: center;
    justify-content: center;
`;

const decorations = (
    <StyledSpinnerContainer>
        <StyledSpinner />
    </StyledSpinnerContainer>
);

export const Wait: React.FC = () => {
    const text = useSelector(({ waitPage }) => waitPage.text);

    return (
        <DialogPage decorations={decorations}>
            <DialogTitle>{text}</DialogTitle>
        </DialogPage>
    );
};
