import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { OrderStatusCategory } from 'store/constants/orderStatus';
import { Wait } from 'pages/Wait/Wait';
import { DialogPage, DialogTitle, DialogButton, FullscreenDecoration } from 'components/DialogPage/DialogPage';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonViews } from 'components/Button/Button';

import pendingSvg from './StatusIssue.assets/pending.svg';
import deliveringSvg from './StatusIssue.assets/delivering.svg';
import successSvg from './StatusIssue.assets/success.svg';
import failedSvg from './StatusIssue.assets/failed.svg';

const mapCategoryToBackground: Record<OrderStatusCategory, string> = {
    [OrderStatusCategory.PENDING]: pendingSvg,
    [OrderStatusCategory.DELIVERING]: deliveringSvg,
    [OrderStatusCategory.SUCCESS]: successSvg,
    [OrderStatusCategory.FAILED]: failedSvg,
};

interface StyledDecorationProps {
    type: OrderStatusCategory;
}
const StyledDecoration = styled(FullscreenDecoration)<StyledDecorationProps>`
    background-image: url('${({ type }) => mapCategoryToBackground[type]}');
`;

export const StatusIssue: React.FC = () => {
    const dispatch = useDispatch();

    const isLoaded = useSelector((state) => state.orderStatusIssuePage.isLoaded);
    const title = useSelector((state) => state.orderStatusIssuePage.title);
    const orderStatusCategory = useSelector((state) => state.orderStatusIssuePage.orderStatusCategory);

    const isFailed = orderStatusCategory === OrderStatusCategory.FAILED;

    const buttons: React.ReactNode = React.useMemo(() => {
        const result = [
            <DialogButton
                shouldFocusOnMount={!isFailed}
                key="dialog-button-details"
                onClick={() => {
                    dispatch(push(ROUTES.STATUS_DETAILS));
                }}
            >
                Детали заказа
            </DialogButton>,
            <DialogButton
                shouldFocusOnMount={isFailed}
                key="dialog-button-report"
                view={isFailed ? ButtonViews.primary : ButtonViews.secondary}
                onClick={() => {
                    dispatch(push(ROUTES.STATUS_REPORT));
                }}
            >
                Связаться с нами
            </DialogButton>,
        ];

        return isFailed ? result.reverse() : result;
    }, [dispatch, isFailed]);

    const decoration = React.useMemo(() => <StyledDecoration type={orderStatusCategory} />, [orderStatusCategory]);

    if (!isLoaded) {
        // TODO: Заменить на скелет https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-243
        return <Wait />;
    }

    return (
        <DialogPage buttons={buttons} decorations={decoration}>
            <DialogTitle>
                <MultilineText text={title} />
            </DialogTitle>
        </DialogPage>
    );
};
